.jobHeadings{
    width: 100%;
    border-top-left-radius: 12px;
    border-top-right-radius: 12px;
    padding: 0 15px;
    padding-bottom: 25px;
    box-shadow: 0 5px 5px gainsboro;
}
.titlee{
    margin-top: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.comp>p{
    margin: 0;
}
.rating{
    margin-bottom: 18px !important;
    margin-right: 10px !important;
}
.app a{
    color: white;
}
.app a:hover{
    color: white;
}
.comp>h4{
    font-size: 15px;
    margin-bottom: 0;
    /* margin:  -15px 0; */
 display: flex;
 align-items: center;
}
.jobHeadings >h4{
    font-size: 18px;
    margin-bottom: -10px !important;
    /* margin:  -15px 0; */
 display: flex;
 align-items: center;
}
.jobHeadings h3{
    margin-bottom: 0 !important;
}

.comp{
    display: flex;
    height: 30px;
    align-items: center;
}
.apply{
    margin-top: 25px;
   display: flex;
   align-items: center;
}
.app{
    width: 150px;
    height: 41px;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    font-weight: bold;
    cursor: pointer;
    background-color: #2557a7;
    border: 0.0625rem solid transparent !important;
    color: #fff;
    padding-bottom: 2px;
}
.app:hover{
    background-color: rgb(0,58,155);
   
}
.detailed{
    height: 400px;
    overflow-y: scroll;
    padding: 0 15px;
    font-size: 14px;
}
.apply img{
    margin-left: 16px;
    margin-top: 0;
    cursor: pointer;
    height: 47px;
    border-radius: 12px;
}
#expandd{
    position: absolute;
    top: 35px;
    right: 0;
    width: 150px;
    background-color: white !important;
   }
   .hideit{
       display: none;
   }